.brands {
	padding-top: 20px;
    padding-bottom: 20px;
	//border: 20px solid $border;
    background-image: linear-gradient(78deg, rgba(166, 188, 240, 0.6) 0%, rgba(194, 212, 253, 0.6) 20%, rgba(255, 255, 255, 0.6) 100%);
	text-align: center;
}
.information {
    padding-top: 100px;
    padding-bottom: 50px;
    margin-left: 20px;
    margin-right: 20px;
    background-color: $white;
	&__comment {
        color: $almost-black;
        font-family: 'Avenir Heavy', sans-serif;
        font-size: 14px;
        font-weight: 800;
        text-transform: uppercase;
        letter-spacing: 2.45px;
        @media #{$desktop} {
            font-size: 12px;
        }
	}
	&__title {
        padding-top: 22px;
        font-family: 'Adobe Caslon Pro', sans-serif;
        font-size: 46px;
        font-weight: 400;
        line-height: 56px;
        letter-spacing: -0.69px;
        @media #{$desktop} {
            font-size: 44px;
        }
    }
    &__line {
    	margin-top: 42px;
    	display: inline-block;
    	content: '';
    	width: 26px;
    	height: 7px;
    	background-color: $light-orange;
    	transform: rotate(-16deg);

    }
    .brand-img {
        padding: 53px 230px;
        @media #{$desktop} {
            padding: 78px 30px;
        }
		&__row {
			width: 100%;
			height: 109px;
			display: flex;
            align-items: center;
		}
		&__col {
			width: 20%;
		}
    }
}
