.sign {
    background-color: $light-grey;
    padding-top: 82px;
    padding-bottom: 53px;
    position: relative;
    &:before {
        position: absolute;
        content: '';
        width: 130px;
        height: 126px;
        background-image: url("../img/stamp.png");
        bottom: -39px;
        right: 196px;
        @media #{$desktop} {
            bottom: -36px;
            right: 43px;
        }
    }
}
.sign-up {
    text-align: center;

    &__title {
        font-family: 'Adobe Caslon Pro', sans-serif;
        font-size: 46px;
        font-weight: 400;
        line-height: 46px;
        letter-spacing: -0.69px;
        @media #{$desktop} {
            font-size: 44px;
        }
    }
    &__comment {
        padding-top: 17px;
        display: block;
        color: $grey;
        font-family: 'Avenir Heavy', sans-serif;
        font-size: 12px;
        font-weight: 800;
        text-transform: uppercase;
        letter-spacing: 1.3px;
        @media #{$desktop} {
            font-size: 12px;
        }
    }
    &__btn {
        position: relative;
        margin-top: 28px;
        border-radius: 29px;
        background-color: $pink;
        color: $white;
        font-family: 'Avenir Black', sans-serif;
        font-size: 13px;
        font-weight: 700;
        letter-spacing: 0.59px;
        padding: 21px 65px 21px 94px;
        @media #{$desktop} {
            font-size: 12px;
        }
        &:before {
            position: absolute;
            content: '';
            top: 16px;
            left: 57px;
            width: 30px;
            height: 30px;
            background-image: url("../img/instagram-logo-black-transparent.png");
            background-repeat: no-repeat;
        }

    }
    &__available {
        padding-top: 30px;
        display: flex;
        justify-content: center;
        span {
            color: #787575;
            font-family: 'Avenir Medium', sans-serif;
            font-size: 19px;
            font-weight: 500;
            line-height: 29px;
            letter-spacing: 0.65px;
            @media #{$desktop} {
                font-size: 17px;
            }
        }
        div {
            margin-left: 20px;
            &:nth-child(odd) {
                margin-top: 5px;
            }
        }
    }
}
.white {
    padding-top: 116px;
    background-color: $white;
    padding-bottom: 109px;
    &:after {
        display: none;
    }
    &:before {
        position: absolute;
        content: '';
        width: 130px;
        height: 126px;
        background-image: url("../img/stamp.png");
        bottom: 18px;
        right: 195px;
        @media #{$desktop} {
            right: 52px;
        }
    }
}
